@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  background-color: #f2f2f2;
}

@layer base {
  ul,
  ol {
    list-style: revert;
  }
}

@layer components {
}

/** Hide Google Maps footer, copyright etc. */
.gm-style-cc {
  display: none;
}

[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  appearance: none;
  height: 12px;
  width: 12px;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 12px 12px;
}
